/*/GENERAL*/

.navbar-toggler-icon{
  width: 1em !important;
  height: 1em 
}


a:link {
  color: #1f181886; 
  text-decoration: none;
}
  

a:visited {
  color: #2b282871; }

a:focus {
 
  background: #615d5d00; 
  
}


a:hover {
  text-decoration:underline;
  color: #2423236b !important; }




/*/ LOGO /*/
.logo{
  width: 65px;
  height: 65px;
  margin-top: 10px ;
  
 
  

}

/*/DETAIÃ‘/*/

.detailNombre, .precio{
  font-size: 104%;
  font-family: 'Josefin Sans', sans-serif;
  color: #8f6d06;
  padding: 10px;
}

.precio, .detailCaracteristica, .total{
  text-align: center;
}


/*/CART/*/
.cabezal{
  display: flex;
 justify-content: center;


}
.filaDescripcion{
  background-color: rgba(41, 85, 70, 0.103);
  text-align: center;
  padding-right: 10px;
  padding-left: 10px;
  
}

.imgCart{
  border-radius:180px;
}
.colorFila{
  background-color: #ffc10710;
  border: thin solid #755b05a8;
  text-align: center;
  vertical-align: middle;
  padding-right: 10px;
  padding-left: 10px;
 
}

/*/FROM CART/*/

.from{
  margin: 10px;
  padding: 10px;
  padding-left: 10px;
}
.fondoFrom{
  margin-top: 30px;
  background-color: rgba(127, 255, 212, 0.199);
}


/*/NAVBAR/*/

.barra a{
  padding: 0;
  margin: 7px;
  
}

.barra, .missAtomic{
  
 display: flex;
 justify-content: center;
 margin: 5px;
 
}

.cartWidget img{
  margin: 5px 5px;
  width: 35px;
  height: 35px;
  
}


/*/ITEM/*/

.itemList, .destacados {
  margin: 5%;
  display: flex;
  justify-content: center;
  
  

}


.itemList h2 font{
  background-color: rgba(0, 255, 255, 0.452);
 
}

.imgCard{
  width:300px;
    height:300px;
    border-radius:160px;
    border:20px solid rgba(102, 102, 102, 0.199);
}

.titulo{
  color:rgb(75, 80, 77);
 
  

}

.btn-outline-warning {
  color: #ffc107;
  border-color: #ffc10700 !important;
}

.btn-outline-warning:hover {
  color: #212529;
  background-color: #ffc10757 !important;
  border-color: #ffc10700 !important ;
  border-radius: 140px;
}

.btn-outline-warning:focus, .btn-outline-warning.focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 193, 7, 0.5);
}

.btn-outline-warning.disabled, .btn-outline-warning:disabled {
  color: #ffc107;
  background-color: transparent;
}

.btn-outline-warning:not(:disabled):not(.disabled):active, .btn-outline-warning:not(:disabled):not(.disabled).active,
.show > .btn-outline-warning.dropdown-toggle {
  color: #212529;
  background-color: #ffc107;
  border-color: #ffc107;
}

.btn-outline-warning:not(:disabled):not(.disabled):active:focus, .btn-outline-warning:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-warning.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 193, 7, 0.5);
}


/*/CONTACTO/*/

.imgContact{
  height: 40%;
  width:40%;
  text-align: center;
}

.mapa{
 text-align: end;
}

.contacto{
  margin-top: 60px;
}

.imgCarru{
  border-radius: 180px;
  height: 20%;
  width: 20%;

}

.conProduct{
  margin-top: 40px;
  
}

.pie{
  margin-top: 30px;
  text-align: center;
  color: #2125297c;
}